/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  background-color: #e6e6e6;
  color: #575757;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

*, *:before, *:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

h1, h2 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

p {
  font-size: 2rem;
}

.titulo-pagina {
  margin: 5rem 0;
}

.boton {
  display: block;
  background-color: #00b3ad;
  border-radius: 5rem;
  padding: 2rem;
  color: #1a1a1a;
  font-weight: 700;
  font-size: 2rem;
  text-align: center;
  margin: 2.5rem 0;
  border: 2px solid #00b3ad;
  -webkit-box-shadow: 0px 0px 24px -5px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 24px -5px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 24px -5px rgba(0, 0, 0, 0.75);
  transition: 300ms all;
}
@media (min-width: 768px) {
  .boton {
    display: inline-block;
  }
}
.boton:hover {
  background-color: transparent;
  color: #575757;
  cursor: pointer;
  -webkit-box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.75);
}

.header {
  background-color: #1a1a1a;
  padding: 1rem 0 3rem 0;
}
@media (min-width: 768px) {
  .header {
    padding: 2rem 0;
  }
}
.header .contenedor-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .header .contenedor-header {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header .contenedor-header .nombre-pagina {
  font-size: 2.5rem;
  font-weight: 700;
  color: #ff7100;
  transition: 500ms color;
}
.header .contenedor-header .nombre-pagina:hover {
  color: #d86000;
}
.header .contenedor-header .contenedor-nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5rem;
}
@media (min-width: 768px) {
  .header .contenedor-header .contenedor-nav {
    flex-direction: row;
  }
}
.header .contenedor-header .contenedor-nav .utilidades {
  visibility: hidden;
}
.header .contenedor-header .contenedor-nav .navegacion {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
}
@media (min-width: 768px) {
  .header .contenedor-header .contenedor-nav .navegacion {
    flex-direction: row;
  }
}
.header .contenedor-header .contenedor-nav .navegacion a {
  color: #e6e6e6;
  transition: 500ms color;
}
.header .contenedor-header .contenedor-nav .navegacion a:hover {
  color: #ff7100;
}

.sobreMi {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.sobreMi .contenedor-sobreMi {
  margin-bottom: 5rem;
}
.sobreMi .contenedor-sobreMi .titulo-sobreMi {
  margin-bottom: 5rem;
}
.sobreMi .contenedor-sobreMi .texto-sobreMi .presentacion {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.sobreMi .contenedor-sobreMi .texto-sobreMi .presentacion .imagen-sobreMi {
  width: 50%;
  margin-bottom: 2.5rem;
  transition: 500ms width;
}
@media (min-width: 768px) {
  .sobreMi .contenedor-sobreMi .texto-sobreMi .presentacion .imagen-sobreMi {
    width: 20%;
  }
}
.sobreMi .contenedor-sobreMi .texto-sobreMi .presentacion .imagen-sobreMi:hover {
  width: 65%;
}
@media (min-width: 768px) {
  .sobreMi .contenedor-sobreMi .texto-sobreMi .presentacion .imagen-sobreMi:hover {
    width: 25%;
  }
}

.portafolio {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5rem;
}
.portafolio .proyectos {
  display: flex;
  flex-direction: column;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .portafolio .proyectos {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .portafolio .proyectos {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.portafolio .proyectos .proyecto {
  background-color: #ff7100;
  border-bottom-left-radius: 2rem;
  border-bottom-right-radius: 2rem;
  margin-bottom: 5rem;
  box-shadow: 0.2px 4px 5px rgba(0, 0, 0, 0.3);
}
@media (min-width: 768px) {
  .portafolio .proyectos .proyecto {
    margin-bottom: 0;
  }
}
.portafolio .proyectos .proyecto .enlaces {
  background-color: #ff7100;
  padding: 2rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-bottom-left-radius: 2rem;
  border-bottom-right-radius: 2rem;
  margin-bottom: 0.5rem;
}
.portafolio .proyectos .proyecto .enlaces a {
  color: #1a1a1a;
  font-weight: 700;
  transition: 300ms color;
}
.portafolio .proyectos .proyecto .enlaces a:hover {
  color: #006662;
}

.contenedor-servicios {
  display: flex;
  flex-direction: column;
  text-align: left;
  align-items: center;
}
.contenedor-servicios .servicios {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 2.5rem;
}
@media (min-width: 768px) {
  .contenedor-servicios .servicios {
    flex-direction: row;
    gap: 5rem;
  }
}
.contenedor-servicios .servicios .servicio {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.contenedor-servicios .servicios .servicio .imagen-servicio {
  border: 1px solid #575757;
  border-radius: 50%;
  padding: 5rem;
  transition: 300ms all;
}
.contenedor-servicios .servicios .servicio .imagen-servicio:hover {
  background-color: #ff7100;
  border: 1px solid #d86000;
}
.contenedor-servicios .servicios .servicio .imagen-servicio img {
  width: 10rem;
}
.contenedor-servicios .boton {
  margin-top: 5rem;
}

.formulario {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5rem;
  padding: 2rem;
  border-radius: 2.5rem;
}
@media (min-width: 768px) {
  .formulario {
    margin-top: 7.5rem;
  }
}
.formulario .contenedor-inputs {
  display: flex;
  flex-direction: column;
  gap: 5rem;
  width: 100%;
}
@media (min-width: 768px) {
  .formulario .contenedor-inputs {
    display: grid;
    gap: 2.5rem;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 5rem;
    width: 75%;
  }
}
.formulario .contenedor-inputs .campo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
@media (min-width: 768px) {
  .formulario .contenedor-inputs .campo {
    flex-direction: row;
    align-items: center;
  }
  .formulario .contenedor-inputs .campo label {
    flex: 0 0 8rem;
  }
  .formulario .contenedor-inputs .campo input {
    flex: 1;
  }
}
.formulario .contenedor-inputs .campo label {
  font-weight: 700;
}
.formulario .contenedor-inputs .campo input {
  border: none;
  background-color: #c0c0c0;
  border-radius: 3rem;
  padding: 2rem;
}
.formulario .campo-textarea {
  margin-top: 2.5rem;
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 1rem;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .formulario .campo-textarea {
    flex-direction: row;
    align-items: center;
    width: 75%;
  }
  .formulario .campo-textarea label {
    flex: 0 0 8rem;
  }
  .formulario .campo-textarea textarea {
    flex: 1;
  }
}
.formulario .campo-textarea label {
  font-weight: 700;
}
.formulario .campo-textarea textarea {
  border: none;
  background-color: #c0c0c0;
  border-radius: 3rem;
  padding: 2rem;
  height: 15rem;
  margin-bottom: 5rem;
}

.footer {
  background-color: #1a1a1a;
  padding: 2.5rem 0;
  margin-top: 5rem;
}
.footer .contenedor-footer {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .footer .contenedor-footer {
    flex-direction: row;
  }
}
.footer .contenedor-footer h3 {
  display: none;
  color: #ff7100;
}
@media (min-width: 768px) {
  .footer .contenedor-footer h3 {
    display: block;
  }
}
.footer .contenedor-footer .texto-copy {
  color: #e6e6e6;
}
.footer .contenedor-footer .redes {
  display: flex;
  gap: 0.5rem;
}
.footer .contenedor-footer .redes a {
  width: 7rem;
  height: 7rem;
}
.footer .contenedor-footer .redes a img {
  width: 5rem;
  border-radius: 5rem;
  transition: 300ms width;
}
.footer .contenedor-footer .redes a img:hover {
  width: 7rem;
}